<template>
  <!-- <div>
    {{ this.$route.query.barcode }}
  </div>
  <div>
    <img :src="info.data.product.image_url" alt="img" />
  </div> -->
  <app-layout>
    <template v-slot:header>
      <product-bar :productName="productName"  />
    </template>

    <template v-slot:content>
      <product-content
        :origin="origin"
        :barcode="barcode"
        :season_start="season_start"
        :season_end="season_end"
        :description="description"
        :info="info"
      />
    </template>
  </app-layout>
</template>

<script>
import axios from "axios";
import AppLayout from "../layout/AppLayout.vue";
import ProductBar from "./ProductBar.vue";
import ProductContent from "./ProductContent.vue";

export default {
  name: "Product",
  components: { AppLayout, ProductBar, ProductContent },
  data() {
    return {
      info: {},
      productName: "",
      origin: "",
      barcode: "",
      season_start: "",
      season_end: "",
      description: "",
    };
  },
  created() {
    axios
      .get(
        "https://world.openfoodfacts.org/api/v0/product/" +
          this.$route.params.barcode +
          ".json"
      )
      .then((response) => {
        this.info = response.data.product;
        console.log(this.info);
        });
    //a changer quand l'api marche
    this.productName = "Product Name";
    this.barcode = "9999999";
    this.season_start = "01/02";
    this.season_end = "01/09";
    this.description =
      "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Soluta eius fugiat a veritatis, culpa distinctio perferendis, qui pariatur hic, quisquam natus deserunt quo id temporibus ea rem mollitia doloremque! Nulla!";
  },
};
</script>

