import Dexie, { Table } from "dexie"
import { LocalDatabaseProductEntry } from "../types/LocalDatabaseProductEntry"
import { Product } from "../types/Product"


export default class LocalDatabase extends Dexie {

    products!: Table<LocalDatabaseProductEntry>
    cart!: Table<{ index: number, product: Product }>


    constructor() {
        super("localDatabase")

        this.version(1).stores({
            products: "++productRef, product",
            cart: "++index, product",
        })
    }

}
